@import "common/app";
@import "common/fonts";

$mobile: "screen and (max-width: 768px)";

html {
  background: #32385b;
  height: 100%;
}

body {
  height: 100%;
  background: url(../images/background.jpg) center top / cover;
}

.logo {
  text-align: center;
  padding-top: 50px;

  img {
    width: 600px;
    margin-right: -50px;

    @media #{$mobile} {
      width: 250px;
      margin-right: -20px;
    }
  }
}

.video {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;

  video {
    display: block;
    min-width: 100%;
    min-height: 100%;
  }
}

.content {
  position: relative;
  z-index: 2;
  background: rgba(50, 56, 91, .5);
  min-height: 100%;
}

.desc {
  text-align: center;
  font-weight: 500;
  font-size: 22px;
  color: #fff;

  @media #{$mobile} {
    font-size: 16px;
  }

  table {
    margin: 0 auto;

    td {
      padding: 0 5px;
    }
  }
}

.text {
  width: 700px;
  margin: 0 auto;
  text-align: center;
  color: #fff;

  @media #{$mobile} {
    width: auto;
    padding: 0 25px;
  }

  h1 {
    font-weight: 300;
    font-size: 30px;

    @media #{$mobile} {
      font-size: 20px;
    }
  }

  a {
    color: inherit;
  }

  p {
    font-size: 18px;

    @media #{$mobile} {
      font-size: 16px;
    }
  }
}